@import url("https://fonts.googleapis.com/css2?family=Porter+Sans+Block:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-montserrat: Montserrat;
  --font-porter-sans-block: "Porter Sans Block";

  /* font sizes */
  --font-size-lg: 18px;
  --font-size-mini: 15px;
  --font-size-6xl: 25px;
  --font-size-31xl: 50px;
  --font-size-base: 16px;
  --font-size-41xl: 60px;
  --font-size-11xl: 30px;
  --font-size-xl: 20px;
  --font-size-xs: 12px;
  --font-size-sm: 14px;

  /* Colors */
  --color-gray-100: #212026;
  --color-white: #fff;
  --color-blueviolet: #753bd9;

  /* border radiuses */
  --br-51xl: 70px;
  --br-5xl: 24px;
  --br-31xl: 50px;
}
